<template>
    <v-container class="pa-0" style="position: relative;">
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon color="primary"
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{settingsText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card style="margin-top: 60px;">
            <v-list two-line active-class="primary--text" style="padding: 0px!important">
                <v-list-item @click="$router.push({name: 'settingsAccount'})">
                    <v-list-item-content>
                        <v-list-item-title>{{accountText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>{{permissionsText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="wantToLogout">
                    <v-list-item-content>
                        <v-list-item-title class="red--text">{{toLogOutText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>{{removeAccountText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-card>

        <v-dialog
                v-model="dialog"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <h3 style="margin-bottom: 10px">{{aboutToLogOutText}}</h3>
                    <p style="margin-bottom: 7px">{{askingForLogOutConfirmationText}}</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="dialog = false">{{cancelText}}</v-btn>
                    <v-btn color="primary" @click="logoutUser">{{confirmText}}</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>

</template>

<script>
    import {mapActions, mapMutations, mapState} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "ProfileHome",
        data: () => ({
            dialog: false,
            snackbar: false,
            snackbarText: null,
            cancelText: Constants.TEXT_CANCEL,
            toLogOutText: Constants.TEXT_TO_LOG_OUT,
            settingsText: Constants.TEXT_SETTINGS,
            permissionsText: Constants.TEXT_PERMISSIONS,
            editText: Constants.TEXT_EDIT,
            closeText: Constants.TEXT_CLOSE,
            accountText: Constants.TEXT_ACCOUNT,
            aboutToLogOutText: Constants.TEXT_YOU_ARE_ABOUT_TO_LOG_OUT,
            confirmText: Constants.TEXT_CONFIRM,
            askingForLogOutConfirmationText: Constants.TEXT_ARE_YOU_SURE_YOU_WANT_TO_LOG_OUT,
            removeAccountText: Constants.TEXT_REMOVE_ACCOUNT
        }),
        computed: {
            ...mapState('user', ['username', 'description',
                'knownLanguages', 'languagesToLearn', 'avgRating', 'numRatings']),

        },

        methods: {
            ...mapActions('user', ['logout']),
            wantToLogout() {
                let self = this;
                self.dialog = true;
            },
            logoutUser() {
                let self = this;

                self.logout().then(() => {
                    self.$store.dispatch('resetAppState').then(() => {
                        self.$router.replace('home');
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
        },
    }
</script>

<style scoped>

</style>
